import gsap from 'gsap'

export default class animation {
    constructor() {

        const content = document.querySelector('.page')
        const sectionNodes =[...document.querySelectorAll('.section')]

        const item1 = document.querySelector('.wrap')
        const item2 = document.querySelector('.h1__txt')
        const item3 = document.querySelector('.p__txt_1')
        const item4 = document.querySelector('.wrap_2')
        const item5 = document.querySelector('.h1__txt_2')
        const item6 = document.querySelector('.p__txt_2')

        const t1 = gsap.timeline({ paused: true })
        const t2 = gsap.timeline({ paused: true })
        const t3 = gsap.timeline({ paused: true })
        const t4 = gsap.timeline({ paused: true })
        const t5 = gsap.timeline({ paused: true })
        const t6 = gsap.timeline({ paused: true })

        t1.from(
            item1,
            {
              // opacity: 0,
              scale: 1.2, 
              duration: 2.5,
              ease: "slow(0.7, 0.7, false)"
            },
            {
              opacity: 1,
              
            }
        )

        t2.fromTo(
            item2,
            {
                y: '100%',
                opacity: 0,
            },
            {
                y: '0%',
                opacity: 1,
                delay: 0.5,
                duration: 1.7,
                ease: "slow(0.7, 0.7, false)"
            }
        )

        t3.fromTo(
            item3,
            {
                y: '100%',
                opacity: 0,
            },
            {
                y: '0%',
                opacity: 1,
                delay: 0.5,
                duration: 1.7,
                ease: "slow(0.7, 0.7, false)"
                }
        )

        t4.from(
            item4,
            {
              opacity: 0,
              scale: 1.2, 
              duration: 2.5,
              ease: "slow(0.7, 0.7, false)"
            },
            {
              opacity: 1,
              
            }
        )

        t5.fromTo(
            item5,
            {
                y: '100%',
                opacity: 0,
            },
            {
                y: '0%',
                opacity: 1,
                delay: 0.5,
                duration: 1.7,
                ease: "slow(0.7, 0.7, false)"
            }
        )

        t6.fromTo(
            item6,
            {
                y: '100%',
                opacity: 0,
            },
            {
                y: '0%',
                opacity: 1,
                delay: 0.5,
                duration: 1.7,
                ease: "slow(0.7, 0.7, false)"
                }
        )



        
        const timelines = [t1, t2, t3, t4, t5, t6, ];

    const INTERSECTION_OPTIONS ={
      threshold: 0.3,
      marginRoot: '0% 0% 0% 0%'
    };

    const callback = (entries) => {
      entries.forEach((entry) => {
        const index = sectionNodes.indexOf(entry.target);

        if (entry.isIntersecting) {
          content.className = "page";

          // start animations
          timelines[index].restart();
          } else {
            timelines[index].reverse();
        }
      });
    };

    const observer = new IntersectionObserver(callback, INTERSECTION_OPTIONS);
    const sections = [...document.querySelectorAll('.section')];
    sections.forEach(section => observer.observe(section));


    }
}
